@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main-container {
  flex-grow: 1;
}

.header {
  background-color: #2a2a2a;
}

.header__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
  align-items: center;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header__logo {
  margin-right: 50px;
}

.header__contacts {
  margin: 0;
  margin-left: auto;
  padding: 0;
  list-style-type: none;
  max-width: 395px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 50px;
  font-size: 18px;
  line-height: 24px;
}

.header__contacts-link {
  color: #ffffff;
  text-decoration: none;
}

.logo__image {
  display: block;
}

.telephone {
  display: flex;
  align-items: center;
  gap: 11px;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
}
.telephone::before {
  content: "";
  width: 14px;
  height: 14px;
  background-image: url("../img/sprite.svg#phone");
}

.navigation {
  font-weight: 500;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-width: 360px;
  display: flex;
  flex-wrap: wrap;
  gap: 49px;
  font-size: 18px;
  line-height: 24px;
}

.navigation__link {
  position: relative;
  padding: 5px 0;
  color: #ffffff;
  text-decoration: none;
}
.navigation__link--current::before {
  position: absolute;
  left: 0;
  bottom: 0;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #ffffff;
}

.button {
  display: inline-flex;
  align-items: center;
  padding: 9px 20px;
  border-radius: 20px;
  border: none;
  outline: 1px solid #646464;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  color: #2a2a2a;
  text-decoration: none;
  cursor: pointer;
}
.button:hover {
  background-color: #2a2a2a;
  color: #ffffff;
  outline: 1px solid #2a2a2a;
}
.button:focus {
  background-color: #2a2a2a;
  color: #ffffff;
  outline: 2px solid #2a2a2a;
}
.button:active {
  background-color: #2a2a2a;
  color: #ffffff;
  outline: 1px solid #2a2a2a;
  opacity: 0.7;
}
.button--alt {
  outline: 1px solid #efefef;
  color: #ffffff;
}
.button--alt:hover {
  background-color: #ffffff;
  color: #2a2a2a;
  outline: 1px solid #efefef;
}
.button--alt:focus {
  background-color: #ffffff;
  color: #2a2a2a;
  outline: 1px solid #efefef;
}

.products {
  background-color: #f5f5f5;
}

.products__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 50px 0 40px;
  display: flex;
  flex-direction: column;
}

.products__title {
  margin: 0;
  margin-bottom: 48px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 48px;
  color: #2a2a2a;
  text-align: center;
}

.products__list {
  padding: 0;
  margin: 0;
  margin-bottom: 47px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.products__link {
  align-self: center;
}

.product-card {
  position: relative;
  background-color: #ffffff;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
}

.product-card__link {
  display: block;
  color: #2a2a2a;
  text-decoration: none;
}

.product-card__name {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}

.product-card__address {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.product-card__image {
  display: block;
}

.product-card__wrapper {
  padding: 30px 40px 40px;
}

.product-card__description-list {
  margin: 0;
  margin-top: 18px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.product-card__description-item {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 5px;
}
.product-card__description-item--guests::after {
  content: "";
  width: 12px;
  height: 16px;
  mask-image: url("../img/sprite.svg#guests");
  background-color: #1b1b1c;
}
.product-card__description-item--guests:hover::after {
  background-color: #741c1c;
}
.product-card__description-item--beds::after {
  content: "";
  width: 14px;
  height: 16px;
  mask-image: url("../img/sprite.svg#beds");
  background-color: #1b1b1c;
}
.product-card__description-item--beds:hover::after {
  background-color: #741c1c;
}

.product-card__description-value {
  margin: 0;
}

.product-card__price {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  padding: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  border-radius: 5px;
  color: #ffffff;
  background-color: #2a2a2a;
}

.reviews {
  background-color: #ffffff;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
}

.reviews__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 50px 0 39px;
  display: flex;
  flex-direction: column;
}

.reviews__title {
  margin: 0;
  margin-bottom: 55px;
  font-weight: 700;
  font-size: 32px;
  line-height: 48px;
  color: #2a2a2a;
  text-align: center;
}

.reviews__list {
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

.reviews__pagination-list {
  padding: 0;
  margin: 0;
  margin-bottom: 39px;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}

.reviews__pagination-button {
  padding: 0;
  border: none;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background-color: #c6c6c6;
  cursor: pointer;
}

.reviews__pagination-item--current .reviews__pagination-button {
  background-color: #1b1b1c;
}

.reviews__link {
  align-self: center;
}

.review {
  padding: 40px 60px 40px 40px;
  background-color: #f4efed;
}

.review__text {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #2a2a2a;
}

.review__wrapper {
  display: grid;
  grid-template-columns: 50px 1fr;
  grid-template-rows: repeat(2, 25px);
  gap: 0 15px;
}

.review__avatar {
  display: block;
  grid-row: 1/-1;
}

.review__author {
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  grid-column: 2/-1;
  grid-row: 1/2;
}

.review__date {
  font-size: 14px;
  line-height: 20px;
  grid-column: 2/-1;
  grid-row: 2/-1;
}

.footer {
  background-color: #2a2a2a;
}

.footer__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 71px 0 30px;
  display: flex;
  flex-direction: column;
  gap: 87px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
}

.footer__inner-wrapper {
  display: flex;
}

.footer__logo {
  margin-right: 50px;
  margin-top: -6px;
  align-self: flex-start;
}

.footer__contacts {
  margin: 0;
  margin-left: auto;
  display: flex;
  font-size: 18px;
  line-height: 24px;
}

.footer__contacts-wrapper {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.footer__contacts-link {
  color: #ffffff;
  text-decoration: none;
}

.footer__contacts-address {
  position: relative;
  font-style: normal;
  max-width: 350px;
}
.footer__contacts-address::before {
  position: absolute;
  top: 3px;
  left: -10px;
  transform: translate(-100%);
  content: "";
  width: 14px;
  height: 19px;
  background-image: url("../img/sprite.svg#mapmarker");
}

.footer__license {
  position: relative;
}
.footer__license::before {
  position: absolute;
  top: -35px;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #7b7b7b;
}

.social {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 30px;
  max-width: 175px;
}
.social--contacts {
  gap: 25px;
}
.social--contacts .social__icon {
  fill: #2a2a2a;
}

.social__icon {
  fill: #ffffff;
}

.license {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 21px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
}

.license__link {
  position: relative;
  padding: 4px 0;
  color: #7b7b7b;
  text-decoration: none;
}
.license__link::before {
  position: absolute;
  bottom: 0;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #7b7b7b;
}

.about {
  background-color: #ffffff;
}

.about__wrapper {
  box-sizing: border-box;
  width: 1170px;
  margin: 0 auto;
  padding: 50px 90px 60px 110px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #2a2a2a;
}

.about__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 32px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
}

.about__text {
  position: relative;
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.about__text:not(:last-of-type) {
  margin-bottom: 20px;
}
.about__text::before {
  position: absolute;
  content: "";
  left: -20px;
  width: 2px;
  height: 100%;
  background-color: #2a2a2a;
}

.about__text-paragraph {
  margin: 0;
}
.about__text-paragraph:not(:last-of-type) {
  margin-bottom: 20px;
}

.contacts {
  padding: 50px 0 60px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  color: #2a2a2a;
  background-color: #f5f5f5;
}

.contacts__title {
  margin: 0;
  margin-bottom: 55px;
  font-size: 32px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
}

.contacts__wrapper {
  width: 1170px;
  margin: 0 auto;
  display: flex;
  background-color: #ffffff;
}

.contacts__inner-wrapper {
  padding: 40px 79px 38px 40px;
}

.contacts__text {
  margin: 0;
  margin-bottom: 40px;
  font-size: 24px;
  line-height: 22px;
}

.contacts__info {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 29px;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 24px;
}

.contacts__info-item {
  border-bottom: 1px solid #e8e8e8;
}
.contacts__info-item:first-child {
  padding-bottom: 20px;
}
.contacts__info-item:not(:first-child) {
  padding: 20px 0;
}

.contacts__map-wrapper {
  position: relative;
}
.contacts__map-wrapper::before {
  position: absolute;
  content: "";
  width: 37px;
  height: 50px;
  background-image: url("../img/sprite.svg#map-marker");
  top: 170px;
  left: 265px;
}

.contacts__map-image {
  display: block;
}

.staff {
  background-color: #ffffff;
}

.staff__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 50px 0 100px;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  color: #2a2a2a;
}

.staff__title {
  margin: 0;
  margin-bottom: 40px;
  font-size: 32px;
  line-height: 48px;
  text-align: center;
}

.staff__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  /* display: flex;
  flex-wrap: wrap;
  gap: 30px; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.staff__item {
  display: flex;
  align-items: center;
  gap: 20px;
}

.staff__item-wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.staff__item-image {
  display: block;
}

.staff__item-name {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
}

.staff__item-post {
  font-size: 18px;
  line-height: 24px;
}

.staff__item-contacts {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 24px;
}

.staff__item-contact {
  display: flex;
  align-items: center;
  gap: 10px;
}
.staff__item-contact--phone::before {
  content: "";
  width: 14px;
  height: 14px;
  mask-image: url("../img/sprite.svg#phone");
  background-color: #2c2c2d;
}
.staff__item-contact--phone:hover::before {
  background-color: #741c1c;
}
.staff__item-contact--mail::before {
  content: "";
  width: 14px;
  height: 11px;
  mask-image: url("../img/sprite.svg#mail");
  background-color: #2c2c2d;
}
.staff__item-contact--mail:hover::before {
  background-color: #741c1c;
}

.all-products {
  background-color: #f5f5f5;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
}

.all-products__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding: 50px 0 53px;
}

.all-products__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 32px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
}

.all-products__form {
  padding: 0 200px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
}

.all-products__form-wrapper {
  display: flex;
  gap: 30px;
  margin-bottom: 30px;
}

.all-products__form-group {
  padding: 0;
  margin: 0;
  border: none;
}

.all-products__form-group-wrapper {
  display: flex;
  gap: 5px;
}

.all-products__form-group-label {
  position: relative;
}

.all-products__form-title {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}

.all-products__form-input {
  padding: 11px;
  margin: 0;
  font: inherit;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  border: 1px solid #2a2a2a;
  border-radius: 5px;
}
.all-products__form-input--date:not(:placeholder-shown) + .all-products__form-input-icon {
  fill: #b3b3b3;
}
.all-products__form-input--date:placeholder-shown + .all-products__form-input-icon {
  fill: #1b1b1c;
}

.all-products__form-input[type=radio],
.all-products__form-input[type=checkbox] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.all-products__form-input-label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px;
  background-color: #f5f5f5;
  border-radius: 5px;
  border: 1px solid #e9e9e9;
  color: #e9e9e9;
  cursor: pointer;
}

.all-products__form-input[type=radio]:checked + .all-products__form-input-label,
.all-products__form-input[type=checkbox]:checked + .all-products__form-input-label {
  background-color: #2a2a2a;
  color: #ffffff;
}

.all-products__form-input-icon {
  position: absolute;
  top: 2px;
  right: 15px;
  fill: #1b1b1c;
}

.all-products__form-button {
  align-self: center;
}

.all-products__list {
  padding: 0;
  margin: 0;
  margin-bottom: 47px;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}